import React from 'react';

import history from '../../services/history';

function signup() {
  return (
    <div className="container">
      <strong> signup </strong>
      <button type="button" onClick={() => history.push('/auth/signin')}>Signin</button>
      <button type="button" onClick={() => history.push('/auth/resetPassword')}>Reset Password</button>
    </div>
  )
}

export default signup;