import React from 'react';

import history from '../../services/history';

function signin() {

  const setToken = () => {
    localStorage.setItem('token', 'testando')
    history.push('/app')
  }

  return (
    <div className="container">
      <strong> signin </strong>
      <button type="button" onClick={() => history.push('/auth/signup')}>Signup</button>
      <button type="button" onClick={() => history.push('/auth/resetPassword')}>Reset Password</button>
      <div style={{marginTop: '40x'}}>
        <button onClick={setToken} style={{background: 'red', border: 'none', padding: '15px 0', color: '#fff'}}>Setar token</button>
      </div>
    </div>
  )
}

export default signin;